.root{
    :global{
        display: flex;
        flex-direction: row;
        gap: 253px;

        .col{
            width: 600px;
            margin-top: 156px;
            margin-left: 109px;
            z-index: 2;
            display: flex;
            flex-direction: column;
            gap: 40px;

            h1{
                font-family: 'Widock TRIAL';
                font-size: 48px;
                color: #fff;
            }
            p{
                font-family: 'Kreadon';
                font-size: 32px;
                color: #fff;
            }
        }
        img:nth-of-type(1){
            z-index: 2;
            width: 792.63px;
            height: 1053px;

        }
        img:nth-of-type(2){
            margin-top: -1px;
            z-index: 1;
            position: absolute;
            width: 1729px;
        }
    }
}

@media(max-width: 390px){
    .root{
        :global{
            width: 390px;

            .col{
                margin: 0;
                padding: 30px;

                h1{
                    font-size: 28px;
                }
                p{
                    font-size: 20px;
                }
            }
            img:nth-of-type(1){
                display: none;
            }
            img:nth-of-type(2){
                width: 390px;
            }
        }
    }
}