.root{
    :global{
        margin-top: 285px;
        margin-left: 137px;
        display: flex;
        flex-direction: column;
        gap: 50px;

        h1{
            font-family: 'Widock TRiAL';
            font-size: 48px;
            color: #fff;
        }
        .row{
            display: flex;
            flex-direction: row;
            gap: 46.5px;

            img:nth-of-type(2){
                height: 378px;
                transition: 1s;
            }
            img:nth-of-type(2):active{
                transform: translateX(-350px) scale(2);
            }
        }
    }
}

@media(max-width: 390px){
    .root{
        :global{
            width: 390px;
            margin: 0;
            margin-top: 250px;
            padding: 30px;
            gap: 30px;

            h1{
                font-size: 32px;
            }
            .row{
                gap: 20px;
                flex-direction: column;
            }
        }
    }
}